import {Injectable} from '@angular/core';
import {
	CanActivate,
	ActivatedRouteSnapshot,
	RouterStateSnapshot,
	UrlTree,
	Router
} from '@angular/router';
import {Observable} from 'rxjs';
import {UserService} from '@shared/services/user.service';
import {filter, map} from 'rxjs/operators';

@Injectable({
	providedIn: 'root'
})
export class HotchairsGuard implements CanActivate {

	constructor(private router: Router, private userService: UserService) {
	}

	canActivate(
		next: ActivatedRouteSnapshot,
		state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
		const url: string = state.url;
		return this.checkUser(next, url);
	}

	checkUser(route: ActivatedRouteSnapshot, url: any): Observable<boolean> {
		return this.userService.user$.pipe(filter(user => user !== null), map(user => {
			let triggerOfCheck = false;
			route.data.expectedRole.split(',').forEach((roleForCheck) => {
				user.roles.forEach((role) => {
					if (role.name === roleForCheck) {
						triggerOfCheck = true;
					}
				});
			});
			if (!triggerOfCheck) {
				this.router.navigate(['/home']);
			}
			return triggerOfCheck;
		}));
	}
}
